.container {
    max-width: 1200px;
    padding-left: 15px;
    padding-right: 15px;
    margin: auto;
}

img {
    max-width: 100%;
}

.pageWrapper {
    background: #38393b;
    min-height: 100vh;
}

.appWrapper {
    display: grid;
    grid-template-areas: "h h" "m c";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 15px;
}

.appContent {
    background-color: #f3f3f3;
    grid-area: c;
}

.minTitle {
    margin: 0 0 25px;
}

.input {
    background: none;
    padding: 15px;
    border-radius: 5px;
    margin: 0;
    border: none;
    -webkit-transition: .5s;
    -moz-transition: .5s;
    -ms-transition: .5s;
    -o-transition: .5s;
    transition: .5s;
    flex: 0 0 auto;
    pointer-events: none;
}

.input.edit {
    background-color: #fff;
    pointer-events: all;
}